/* General App Style */
.Landing {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column; 
  min-height: 100vh
}
.main-content {
  flex: 1; 
}

.topbar {
  width: 100%;
  height: 100px; 
  background-color: rgb(0, 0, 0);
  border-bottom: 1px solid rgb(37, 37, 37); 
  transition: border-bottom 0.3s ease;
  display: flex;
  align-items: center;
  padding-left: 100px; 
  z-index: 1000;
}

.topbar:hover {
  border-bottom: 1px solid rgb(45, 45, 45);
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.logo-img {
  width: 150px;
  height: auto;
  position: absolute;
  transition: opacity 0.5s ease-in-out; 
  image-rendering: pixelated;
}

.logo-img.logo-hover {
  opacity: 0;
}

/* Separator Styling */
.seperator-img {
  width: 7px;
  height: 35px;
  margin-left: 105px;
  image-rendering: pixelated;
}


.docs {
