.grid-background {
  position: fixed; 
  top: 0;
  left: 0;
  width: 100vw; 
  height: 100vh; 
  background: url('../public/grid.png') repeat;
  animation: moveDiagonal 50s linear infinite;
  z-index: -1; 
}

.grid-background-dark {
  position: fixed; 
  top: 0;
  left: 0;
  width: 100vw; 
  height: 100vh; 
  background: url('../public/grid-darker.png') repeat;
  animation: moveDiagonal 50s linear infinite;
  z-index: -1; 
}

@keyframes moveDiagonal {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 100%;
  }
}

.big-title {
  white-space: pre-line;
  position: absolute;
  top: 120px;
  left: 50px;
  color: rgb(233, 241, 255);
}

.animated-word {
  display: inline-block;
  position: relative;
  overflow: hidden;
  height: 1.2em;
  line-height: 1.2em;
  vertical-align: middle;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}

.slide-out {
  animation: slideOut 0.5s forwards;
}

.slide-in {
  animation: slideIn 0.5s forwards;
}

.slide-out-second {
  animation: slideOutSecond 0.5s forwards;
}

.slide-in-second {
  animation: slideInSecond 0.5s forwards;
}

@keyframes slideOut {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(10%);
    opacity: 0;
  }
}

@keyframes slideIn {
  0% {
    transform: translateY(-10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideOutSecond {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-10%);
    opacity: 0;
  }
}

@keyframes slideInSecond {
  0% {
    transform: translateY(10%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.big-subtitle {
  font-family: Geist;
  white-space: pre-line;
  position: absolute;
  top: 235px;
  left: 50px;
  font-size: 20px;
  color: rgb(207, 214, 225);
}

.download-button {
  position: absolute;
  top: 320px;
  left: 50px; 
  background-color: #3F8AFF;
  color: white;
  font-size: 18px;
  font-weight: bold;
  padding: 15px 95px;
  border: none;
  border-radius: 0;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.3s ease;
  font-family: PixelFont;
  z-index: 2;
}

.download-button:hover {
  transform: translateY(-3px); 
  box-shadow: 0 4px 15px rgba(0, 0, 255, 0.5);
}

.download-button:active {
  transform: translateY(1px);
}

.dotted-line-container {
  position: absolute;
  top: 385px; 
  left: 280px;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
}

.dotted-line {
  background: url('../public/Lines.png');
  width: 23px;
  height: 37px; 
  position: relative;
  top: -15px;
  image-rendering: pixelated;
  background-size: contain;
  z-index: 1;
  opacity: 0.65;
}

.dotted-arrow {
  background: url('../public/Pointer.png');
  width: 24px; 
  height: 13px;
  position: relative;
  top: 1px;
  left: 5px; 
  image-rendering: pixelated;
  background-size: contain;
  animation: moveLeftRight 3s ease-in-out infinite; 
  opacity: 0.65;
}

@keyframes moveLeftRight {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(5px);
  }
}

.line-text {
  font-size: 16px;
  font-weight: bold;
  color: #dcdcdc;
  position: relative;
  text-transform: uppercase;
  top: 1px;
  left: 26px;
}

