.footer {
  position: fixed;
  left: 0;
  bottom: -100%; /* Start offscreen */
  width: 100%;
  padding: 20px 0;
  background: linear-gradient(to top, #05132D, transparent);
  color: white;
  text-align: center;
  z-index: 1;
  transition: bottom 0.5s ease; /* Smooth transition */
}

.footer.visible {
  bottom: 0; /* Bring the footer into view */
}

.footer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, #05132D, transparent);
  z-index: -1;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-text {
  margin: 0;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
}

.footer-links {
  margin-top: 10px;
}

.footer-link {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  margin: 0 10px;
  font-size: 14px;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: white;
}